.dashboard-body {
  color: #2b323a;
  text-decoration: none;

  background-color: #ffffff;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  -webkit-clip-path: inset(100%);

          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.mentor-select {
  width: 70%;

  margin-bottom: 20px;
}

.find-mentor-button:hover {
  outline: 2px solid #87cefa;
}

.github-auth {
  display: block;
  width: 300px;

  margin: 0 auto;
  margin-bottom: 20px;

  text-align: center;
  color: #c9510c;
}

.dashboard-table {
  margin: 0 auto;
  margin-bottom: 30px;
  font-size: 20px;

  border-collapse: collapse;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.dashboard-table caption {
  margin-bottom: 10px;

  font-size: 25px;
  font-weight: bold;
}

.dashboard-table td,
.dashboard-table th {
  padding: 5px 10px;
  border: 1px solid #2b323a;
}

.dashboard-table a {
  font: inherit;
  color: inherit;
  text-decoration: none;
}

.dashboard-table a:hover,
.dashboard-table a:focus{
  text-decoration: underline;
}

.task-status-in-progress {
  background-color: #faebd7;
}

.task-status-checking {
  background-color: #ff69b4;
}

.task-status-checked {
  background-color: #90ee90;
}

.task-status-todo {
  background-color: #add8e6;
}

.task-status-checking-time-gone {
  background-color: #ff0000;
}

.dashboard-notes p{
  margin: 10px 0;
}

.notes-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.notes-list li:before {
  content: "";

  display: inline-block;
  width: 25px;
  height: 15px;

  margin-right: 15px;

  border: 1px solid #2b323a;
}

.checked-note:before {
  background-color: #90ee90;
}

.todo-note:before {
  background-color: #add8e6;
}

.current-note:before {
  background-color: #faebd7;
}

.need-checked-note:before {
  background-color: #ff69b4;
}

.not-checked-note:before {
  background-color: #ff0000;
}


